<template>
  <section class="flex">
    <div
      v-if="isHiddenNav"
      class="hidden lg:block pr-4 mr-4 py-4 lg:py-10"
    ></div>
    <nav
      class="hidden lg:block pr-4 mr-4 py-4 lg:py-10 overflow-y-scroll lg:h-layout"
      v-else
    >
      <slot name="router"></slot>
      <div class="flex flex-col mt-72 ml-10">
        <router-link
          v-for="{ path, name } in footerLinks"
          :key="name"
          :to="path"
          class="text-link hover:text-main-red mb-2.5"
        >
          {{ name }}
        </router-link>
      </div>
      <LangButtons class="ml-10" />
    </nav>
    <div
      class="flex-1 px-4 lg:px-0 lg:pr-10 flex flex-col overflow-y-scroll h-auto lg:h-layout py-4 lg:py-10"
      ref="main"
    >
      <NavbarLink />
      <div class="flex flex-col lg:flex-row gap-2.5 mb-5 lg:mb-20">
        <div
          class="h-16 lg:h-40 lg:w-1/2 bg-gray-100 flex items-center justify-center"
        >
          ad
        </div>
        <div
          class="h-16 lg:h-40 lg:w-1/2 bg-gray-100 flex items-center justify-center"
        >
          ad
        </div>
      </div>
      <div class="flex-1">
        <slot></slot>
      </div>
      <ADs class="hidden lg:flex" />
    </div>
  </section>
</template>
<script>
import { mapGetters } from "vuex";
import NavbarLink from "@/components/NavbarLink";
import LangButtons from "@/components/LangButtons";
import ADs from "@/components/ADs";
export default {
  components: {
    NavbarLink,
    ADs,
    LangButtons,
  },
  data() {
    return {
      isHiddenNav: false,
    };
  },
  computed: {
    ...mapGetters(["isLogin"]),
    footerLinks() {
      return [
        { path: "/system/link", name: this.$t("system_link") },
        { path: "/system/policy", name: this.$t("system_policy") },
        { path: "/system/service", name: this.$t("system_service") },
        { path: "/system/privacy", name: this.$t("system_privacy") },
        { path: "/system/disclaimer", name: this.$t("system_disclaimer") },
        { path: "/system/contact-us", name: this.$t("system_contact_us") },
      ];
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler: function (value) {
        const isMember = value.matched.find((item) => item.name === "Member");
        if (isMember && !this.isLogin) {
          this.isHiddenNav = true;
          return;
        }
        this.isHiddenNav = false;
      },
    },
    "$route.path": {
      immediate: true,
      handler: function () {
        if (this.$refs.main) {
          this.$refs.main.scrollTo({
            top: 0,
            behavior: "smooth",
          });
        }
      },
    },
  },
};
</script>
