<template>
  <div class="inline-flex items-center gap-3">
    <button
      @click="setLang('cn')"
      class="hover:text-main-red"
      :class="{ 'text-main-red': lang === 'cn' }"
    >
      簡體
    </button>
    <button
      @click="setLang('tw')"
      class="hover:text-main-red"
      :class="{ 'text-main-red': lang === 'tw' }"
    >
      繁體
    </button>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  computed: {
    ...mapGetters(["lang"]),
  },
  methods: {
    ...mapActions(["setLang"]),
  },
};
</script>
