<template>
  <div class="overflow-x-scroll flex flex-nowrap flex-shrink-0 gap-6 mb-5">
    <a
      v-for="(link, index) in links"
      :key="index"
      :href="link.href"
      target="_blank"
      class="underline flex-shrink-0"
      >{{ link.title }}</a
    >
  </div>
</template>
<script>
const links = [];
for (let i = 0; i <= 30; i++) {
  const title = i % 2 === 0 ? " test" : "不良";
  links.push({ title, href: "https://www.google.com" });
}
export default {
  data() {
    return {
      links,
    };
  },
};
</script>
